<template>
  <el-container>
    <el-aside class="aside">
      <el-menu :default-active="this.$route.path" :router="true">
        <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">
          {{ item.navItem }}
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-main>
      <!-- index.js-->
      <router-view />
    </el-main>

  </el-container>

</template>

<script>
  export default {
    name: "Home",
    data() {
      return {
        navList: [{
            name: '/test_list',
            navItem: ''
          },{
            name: '/test',
            navItem: ''
          },
          {
            name: '/test_edit',
            navItem: ''
          },
          {
            name: '/test_suit',
            navItem: ''
          },
          {
            name: '/header',
            navItem: ''
          },
          {
            name: '/suit',
            navItem: ''
          },
          {
            name: '/test_suit_run',
            navItem: ''
          },
          {
            name: '/timer',
            navItem: ''
          },
        ],
      }
    },

    methods: {

    },
  }
</script>
<style scoped>
  .container {
    height: 100vh;
    font-size: 15px;
  }

  .header {
    background: #212121;
    color: #fff;
  }

  .aside {
    background: #3a3a3a;
    color: #fff;
    height: 100%;
  }

  .el-submenu .el-menu-item {
    text-align: right;
  }
</style>



