<template>

  <div>
    <div><h3>--{{run_name}}, {{counter_id}}</h3> </div>
    <div id="chartColumn" style="width: 100%; height: 400px;"></div>
    <el-row>
        {{total}} ,  {{pass}}, {{fail}}
    </el-row>
    <el-row>
      <el-table :data="test_result_lst" style="width: 100%" border :cell-style="green_padd_row">
        <el-table-column prop="case_id" label="id" min-width="100">
          <template slot-scope="scope"> {{ scope.row.case_id }} </template>
        </el-table-column>
        <el-table-column prop="suit_name" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.suit_name }} </template>
        </el-table-column>
        <el-table-column prop="case_name" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.case_name }} </template>
        </el-table-column>
        <el-table-column prop="case_method" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.case_method }} </template>
        </el-table-column>
        <el-table-column prop="case_url" label="Url" min-width="100">
          <template slot-scope="scope"> {{ scope.row.case_url }} </template>
        </el-table-column>
        <el-table-column prop="result_match_type" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.result_match_type }} </template>
        </el-table-column>
        <el-table-column prop="case_expected" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.case_expected }} </template>
        </el-table-column>
        <el-table-column prop="result_response" label="response" min-width="100">
          <template slot-scope="scope"> {{ scope.row.result_response }} </template>
        </el-table-column>
        <el-table-column prop="result_pass" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.result_pass }} </template>
        </el-table-column>
        <el-table-column prop="start_time" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.start_time }} </template>
        </el-table-column>
        <el-table-column prop="end_time" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.end_time }} </template>
        </el-table-column>
        <el-table-column prop="request_time" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.request_time }} </template>
        </el-table-column>
        <el-table-column prop="result_des" label="" min-width="100">
          <template slot-scope="scope"> {{ scope.row.result_des }} </template>
        </el-table-column>
      </el-table>
    </el-row>

  </div>


</template>

<script>
  import * as echarts from 'echarts';

  export default {
    name: 'TestSuitResultReport',
    data() {
      return {
        run_id2: "4",
        test_result_lst: [],
        run_id: 0,
        counter_id: 0,
        run_name: "",
        total: "",
        pass: "",
        fail: "",
      }
    },
    mounted: function() {
      this.run_id = this.$route.query.run_id
      this.counter_id = this.$route.query.counter_id
      console.log("" + this.run_id)
      console.log("" + this.counter_id)
      this.drawLine();
    },
    methods: {
      getting() {
        this.$http.post(this.GLOBAL.BASE_URL + "get_test_result/", {
          "run_id": this.run_id,
          "run_counter": this.counter_id
        }).then((response) => {

          console.log(response.data);
          this.test_result_lst = response.data.data;
          console.log(this.test_result_lst)
        }).catch((error) => {
          console.log(error)
        })
      },
      green_padd_row({
        row,
        column,
        rowIndex,
        columnIndex
      }) {
        var pass_col = 8
        var request_time_col = 11
        // 
        if (row.result_pass === true && columnIndex === pass_col) {
          return {
            background: 'green',
            color: 'white'
          }
        }
         // 
        if (row.result_pass === false && columnIndex === pass_col) {
          return {
            background: 'red'
          }
        }
        // 3s
        if (row.request_time > 3 && columnIndex === request_time_col) {
          return {
            background: 'red',
            color: 'white'
          }
        }
        if (row.request_time < 3 && columnIndex === request_time_col) {
          return {
            background: 'green',
            color: 'white'
          }
        }
      },
      drawLine() {
        this.$http.post(this.GLOBAL.BASE_URL + "get_run_report/", {
          "run_id": this.run_id,
          "run_counter": this.counter_id
        }).then((response) => {

          this.chartColumn = echarts.init(document.getElementById('chartColumn'));
          this.run_name = response.data.run_name
          this.total = response.data.toatal
          this.pass = response.data.pass_count
          this.fail = response.data.fail_count
          this.chartColumn.setOption({
            title: {
              text: ''
            },
            tooltip: {
              trigger: 'item',
              formatter: "{a} <br/>{b} : {d}%"
            },
            type: 'pie', // 
            radius: '55%', //  55% 
            series: [{
              name: '',
              type: 'pie',
              clockwise: 'true',
              startAngle: '0',
              radius: '60%',
              center: ['50%', '50%'],
              // 
              color: ['green', 'red', 'grey'],
              data: [{
                  value: response.data.pass_count,
                  name: '' + response.data.pass_count
                },
                {
                  value: response.data.fail_count,
                  name: '' + response.data.fail_count
                },
                {
                  value: response.data.unkown_count,
                  name: '' + response.data.unkown_count
                }
              ],
            }]
          });

          this.test_result_lst = response.data.data;
        }).catch((error) => {
          console.log(error)
        })

      },
    }
  }
</script>



