<template>

  <el-form :model="test_add" class="demo-ruleForm" ref="form" label-position="left" :rules="rules" size="small">
    <el-row>
      <el-col :span="8">
        <el-form-item label="" label-width="100px">
          <el-select v-model="test_add.suit_id" placeholder="">
            <el-option v-for="item in suit" :key="item.s_id" :label="item.s_name" :value="item.s_id">
            </el-option>
          </el-select>
        </el-form-item>
  
      </el-col>
    </el-row>
  
    <el-row>
      <el-col :span="8">
        <el-form-item label="" label-width="100px">
          <el-input v-model="test_add.case_name" placeholder=""></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <el-form-item label="Url" label-width="100px" prop="case_url">
          <el-input v-model="test_add.case_url" placeholder="Url"></el-input>
        </el-form-item>
  
      </el-col>
    </el-row>
  
    <el-row>
      <el-col :span="8">
        <el-form-item label="" label-width="100px" prop="case_method">
          <el-select v-model="test_add.case_method" placeholder="">
            <el-option v-for="item in t_method" :key="item.t_value" :label="item.t_lable" :value="item.t_value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <el-form-item label="header" label-width="100px" prop="case_header_id">
          <el-select v-model="test_add.case_header_id" placeholder="">
            <el-option v-for="item in t_h" :key="item.h_id" :label="item.h_name" :value="item.h_id">
            </el-option>
          </el-select>
        </el-form-item>
  
      </el-col>
    </el-row>
  
    <el-row>
      <el-col :span="12">
        <el-form-item label="post_json" label-width="100px">
          <el-input type='textarea' v-model="test_add.case_json" @change.native="clearLineBreak" placeholder="json"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
  
    <el-row>
      <el-col :span="12">
        <el-form-item label="post_data" label-width="100px">
          <el-input type='textarea' v-model="test_add.case_data" @change.native="clearLineBreak" placeholder="data"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
  
    <el-row>
      <el-col :span="12">
        <el-form-item label="" label-width="100px">
          <el-input type='textarea' v-model="test_add.source_address" placeholder="" @change.native="clearLineBreak"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
  
    <el-row>
      <el-col :span="12">
        <el-form-item label="" label-width="100px" prop="case_expected">
          <el-input type='textarea' v-model="test_add.case_expected" placeholder="" @change.native="clearLineBreak"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
  
    <el-row>
      <el-col :span="12">
        <el-form-item label="" label-width="100px">
          <el-input type='textarea' v-model="test_add.replace_name" placeholder="" @change.native="clearLineBreak"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
  
    <el-row>
      <el-col :span="8">
        <el-form-item label="" label-width="100px" prop="result_match_type">
  
          <el-select v-model="test_add.result_match_type" placeholder="">
            <el-option v-for="item in t_match_type" :key="item.value" :label="item.lable" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>

      <el-row>
        <el-col :span="8">
          <el-form-item label="" label-width="100px" prop="i_match_type">

            <el-select v-model="test_add.i_match_type" placeholder="">
              <el-option v-for="item in t_match_type" :key="item.value" :label="item.lable" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="3">
          <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
        </el-col>
        <el-col :span="3">
          <el-form-item>
            <el-button type="success" @click="TestNew"></el-button>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item>
        <el-input type='textarea' v-model="msg" placeholder="" rows="4"></el-input>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
  export default {
    name: 'Test',
    data() {
      return {
        input: '',
        test_lst: [],
        msg: '',
        suit: {
          s_id: '',
          s_name: ''
        },
        t_method: [{
            lable: "GET",
            value: 0
          },
          {
            lable: "POST",
            value: 1
          }
        ],
        t_match_type: [{
            lable: "",
            value: 0
          },
          {
            lable: "",
            value: 1
          }
        ],
        t_h: {
          h_id: '',
          h_name: '',
          h_v: ''
        },
        test_add: {
          suit_id: '',
          i_name: '',
          i_url: '',
          i_header: '',
          i_method: '',
          i_json: '',
          i_data: '',
          i_source_address: '',
          i_expected: '',
          i_match_type: '',
          i_replace: ''
        },
        rules: {
          suit_id: [{
            required: true,
            message: '',
            trigger: 'change'
          }],
          // name v-modeldataname
          i_url: [{
            required: true,
            message: 'Url',
            trigger: 'blur'
          }],
          i_method: [{
            required: true,
            message: '',
            trigger: 'change'
          }],
          i_header: [{
            required: true,
            message: 'header',
            trigger: 'change'
          }],
          i_expected: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          i_match_type: [{
            required: true,
            message: '',
            trigger: 'change'
          }],
        }
      }
    },
    mounted: function() {
      this.ShowSuit();
      this.InitSelect();
      this.InitHeader();

    },
    methods: {
      ShowSuit() {
        this.$http.get(this.GLOBAL.BASE_URL + "get_test/").then((response) => {

          console.log(response.data);
          this.test_lst = response.data.data;
          console.log(this.test_lst)
        }).catch((error) => {
          this.$notify({
            title: '',
            message: "",
            type: 'error'
          });
        })
      },
      clearLineBreak(e) {
          // `e`  DOM 
          // @change.native .nativee.target
          var val = e.target.value;
          // alert(e.target.value)
          console.log(val)
          //
          val = val.replace(/\r\n/g,"")
          val = val.replace(/\n/g,"");
          console.log(val)
          e.target.value = val
      },
      TestNew() {
        var is_continue = true;
        this.msg = ""
        this.$refs.form.validate((valid) => {
          if (valid) {

          } else {
            is_continue = false
            this.$notify({
              title: '',
              message: '',
              type: 'error'
            });
            return false;
          }
        });
        if (is_continue) {
          this.$http.post(this.GLOBAL.BASE_URL + "test_test/", this.test_add).then((response) => {

            if (response.data.retcode == 0){
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              });
              this.msg = response.data.msg
            }
            if (response.data.retcode == -1){
              this.$notify({
                title: '',
                message: "",
                type: 'error'
              });
              this.msg = response.data.msg
            }
          }).catch((error) => {
            this.$notify({
              title: '',
              message: "",
              type: 'error'
            });
          })
        }

      },
      onSubmit() {
        this.$refs.form.validate((valid) => {
          if (valid) {

          } else {
            console.log('error submit!!');
            return false;
          }
        });
        this.$http.post(this.GLOBAL.BASE_URL + "save_test/", this.test_add).then((response) => {
          this.$notify({
            title: '',
            message: response.data.msg,
            type: 'info'
          });

        }).catch((error) => {
          this.$notify({
            title: '',
            message: "",
            type: 'error'
          });
        })
      },
      InitSelect() {
        this.$http.post(this.GLOBAL.BASE_URL + "get_suit/", this.suit_add).then((response) => {
          this.suit = response.data.data;

        }).catch((error) => {
          this.$notify({
            title: '',
            message: "",
            type: 'error'
          });
        })
      },
      InitHeader() {
        this.$http.post(this.GLOBAL.BASE_URL + "get_header/", this.suit_add).then((response) => {
          this.t_h = response.data.data;

        }).catch((error) => {
          this.$notify({
            title: '',
            message: "",
            type: 'error'
          });
        })
      },
    }
  }
</script>

<!--  -->
<style lang="css">
</style>



