<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
  export default {
    name: 'App'
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }

  /**/
  .el-input__inner {
    height: 30px;
    line-height: 30px;
  }

  .el-table {
    table-layout: fixed;
  }

  /* table header */
  .el-table__header th,
  .el-table__header tr {

    background-color: #17B3A3;
    color: black;
  }

  .el-select {
    //el-col
    width: 100%;
  }

  /*  */
  .el-table .cell {
    padding: 0.03125rem !important;
    word-break: keep-all;
    /*  */
    white-space: nowrap;
    /*  */
    overflow: hidden;
    /*  */
    text-overflow: ellipsis;
    /* ...overflow:hidden;*/
  }

  /* */
  .el-table .cell:hover {
    white-space: normal;
    overflow: auto;
    text-overflow: clip
  }

  .el-table__body td {}

  ,
  .el-table__body th {


  }

  /*  */

  .el-table__header td {
    height: 1.875rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  ,
  .el-table__header th {
    height: 1.875rem;
    padding: 6px 0px;

  }

  /*  */

  .site-wrapper .el-pagination {

    margin-top: 5px;

    text-align: right;

  }

  .el-pager li.active {

    color: #080909;

    cursor: default;

    background-color: #17B3A3;

    border-radius: 2px;

  }

</style>



